import { GetToken } from "@/utilities/cookies";
import SearchProductList from "@/Components/Search";
import TopCategoriesServer from "@/Components/TopCategories/server";

export default async function Listview(props) {
  const searchParams = await props.searchParams;
  const token = await GetToken();

  return (
    <>
      <TopCategoriesServer />

      <SearchProductList token={token} searchParams={searchParams} />
    </>
  );
}
